.transactionHistoryWrapper {
  padding: 60px;
  background-color: #f5f5f5;
}

.transactionHistory {
  width: 100%;
  border-collapse: collapse;
}

.tableHeader {
  background-color: #481ea2;
  padding: 10px;
  text-align: left;
  font-weight: bold;
}

.tableRow {
  background-color: #55d335;
}

.tableRow:nth-child(even) {
  background-color: #f1f1f1;
}

.tableData {
  padding: 10px;
}
